@media screen and (max-width: 1024px) {
  .title:after, .title:before {
    content: "";
  }
}

@media screen and (max-width: 768px) {
  .img-hover-block {
    margin-left: 20px;
  }

  .design-img {
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    max-width: 100%;
  }
}

@media screen and (max-width: 425px) {
  .res-design {
    flex-direction: column;
  }

  .portfolio {
    padding: 100px 0;
  }

  .design-slider {
    width: 100%;
  }

  .slider-body {
    width: 100%;
    padding: 0;
    overflow: hidden;
  }

  .title {
    font-size: 13px;
  }

  p {
    font-size: 10px;
  }

  h1 {
    font-size: 20px;
  }

  h4 {
    font-size: 14px;
  }

  .block-service {
    flex-direction: column;
    align-items: center;
    display: flex;
  }

  .item1 {
    margin-bottom: 20px;
  }

  .item1 h3:before {
